@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-badge::before {
    background-color: transparent;
    background-size: 100%;
    background-repeat: no-repeat;
  }
  .fr-badge::before {
    width: 1rem;
    height: 1rem;
  }
  .fr-badge--sm::before {
    width: 0.75rem;
    height: 0.75rem;
  }
  .fr-badge.fr-badge--info::before, .fr-badge--sm.fr-badge--info::before {
    background-image: url("../../icons/system/fr--info-fill.svg");
  }
  .fr-badge.fr-badge--success::before, .fr-badge--sm.fr-badge--success::before {
    background-image: url("../../icons/system/fr--success-fill.svg");
  }
  .fr-badge.fr-badge--error::before, .fr-badge--sm.fr-badge--error::before {
    background-image: url("../../icons/system/fr--error-fill.svg");
  }
  .fr-badge.fr-badge--warning::before, .fr-badge--sm.fr-badge--warning::before {
    background-image: url("../../icons/system/fr--warning-fill.svg");
  }
  .fr-badge.fr-badge--new::before, .fr-badge--sm.fr-badge--new::before {
    background-image: url("../../icons/weather/flashlight-fill.svg");
  }
  ul.fr-badges-group {
    list-style-type: none;
  }
  ol.fr-badges-group {
    list-style-type: none;
  }
  ul.fr-badges-group,
  ol.fr-badges-group {
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
  }
}

